<template>
  <div>
    <view-detail></view-detail>
    <comment-write :isbn="isbn" />
    <comment-write v-if="isModifyShow && this.modifyComment != null"
      :modifyComment="this.modifyComment" 
      @modify-comment-cancel="onModifyCommentCancel" />
    <comment v-for="(comment, index) in comments" :key="index" 
      :comment="comment" @modify-comment="onModifyComment" />
  </div>
</template>

<script>
import ViewDetail from "@/components/book/include/ViewDetail.vue";

import { mapGetters } from "vuex";
import CommentWrite from "@/components/book/include/comment/CommentWrite.vue";
import Comment from "@/components/book/include/comment/Comment.vue";

export default {
  data() {
    return {
      isbn: "",
      isModifyShow: false,
      modifyComment: Object
    };
  },
  computed: {
    ...mapGetters(["comments"])
  },
  components: {
    ViewDetail,
    CommentWrite,
    Comment
  },
  created() {
    this.isbn=this.$route.params.isbn;
    this.$store.dispatch("getBook", this.isbn);

    this.$store.dispatch("getComments", this.isbn);
  },
  methods: {
    onModifyComment(comment) {
      this.modifyComment = comment;
      this.isModifyShow = true;
    },
    onModifyCommentCancel(isShow) {
      this.isModifyShow = isShow;
    }
  }
}
</script>

